html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
}

body {
  font-size: calc(1rem + 0.75vw);
  margin: 0;
  padding: 0;
  font-family: "Bai Jamjuree", sans-serif;
}

h1, h2, h3 {
  margin-top: 0;
  text-align: center;
}

h1 {
  font-size: 1.6em;
}

a, a:visited, a:active {
  text-decoration: none;
}

p {
  text-align: center;
  color: #9fabb2;
  font-size: 0.9em;
}

.container {
  display: flex;
  flex-direction: column;
  padding: 0 30px;
  margin: 0 auto;
  max-width: 690px;
  align-items: center;
}
.buttom {
  font-size: 1rem;
  color: #ffffff;
  width: 309px;
  justify-self: center;
  padding: 20px 75px;
  margin-bottom: 25px;
  border-radius: 40px;
  text-align: center;
}
@media (min-width: 40rem) {
  .buttom {
    padding: 17px 35px;
    width: 225px;
  }
}
.buttom-cyan {
  background-color: #26baa4;
  box-shadow: 0px 5px 10px rgba(38, 186, 164, 0.4);
  transition: 250ms ease-in-out;
}
.buttom-cyan:hover {
  background-color: rgba(38, 186, 164, 0.8);
}
.buttom-blue {
  background-color: #6173ff;
  box-shadow: 0px 5px 10px rgba(97, 115, 255, 0.4);
  transition: 250ms ease-in-out;
}
.buttom-blue:hover {
  background-color: rgba(97, 115, 255, 0.8);
}

.header {
  background: url("../../../images/bg-header-mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 20.9375rem;
  display: flex;
  justify-content: center;
}
@media (min-width: 40rem) {
  .header {
    background: url("../../../images/bg-header-desktop.png");
    height: 340px;
    background-position: center;
  }
}
.header-logo {
  margin-top: 125px;
}

h1 {
  margin-bottom: 22px;
}

p {
  margin-bottom: 50px;
}

.history-buttons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 40rem) {
  .history-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 15px;
  }
}